import { Component } from 'react';
import "./Projects.css";
import ggwp from "../Files/ggwp.png"
import road from "../Files/road2.png"
import lake from "../Files/fall_lake.png"
export default class Projects extends Component{


    render(){
        return(
        <div class = "projects">
            <h2>Target Clicker</h2>
            <p>A target clicking program made using Java, the graphical library libgdx, and gradle. 
                The program's goal is to improve a user's mouse familiarity and accuracy.</p>
            <br></br>
            <a href = 'https://github.com/HoffmanCorbin/TargetClicker'>View on github</a>
            <br></br>
            <iframe title = "TargetClicker" src="https://www.youtube.com/embed/1MP1YPsFcGE" 
            frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

            <h2>Weather App</h2>
            <p>A simple weather app created in Python.</p>
            <br></br>
            <a href = 'https://github.com/HoffmanCorbin/weatherApp'>View on github</a>
            <br></br>
            <iframe title = "WeatherApp" src="https://www.youtube.com/embed/M_iwZl9nIhM"
             frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
             <h2>3d Renders</h2>
             <p>These are 3d renders I have created using blender</p>
             <br></br>
             <img src = {lake} alt = "Lake"></img>
             <img src = {ggwp} alt = "GGWP"></img>
             <img src = {road} alt = "Road"></img>
             <br></br>


        </div>);
    }

    handleClick(){
        
    }

}