import { Component } from 'react';
import HeaderButton from './HeaderButton';
import "./Header.css"
import {NavLink }from 'react-router-dom';

export default class Header extends Component{
    render(){
        return(
        
            <nav class = "headerDisplay">
                <NavLink 
                    className="Nav_link" to = '/' exact
                    activeClassName='active' ><HeaderButton name = "Home" /></NavLink>
                <NavLink className="Nav_link"   to = '/AboutMe'> <HeaderButton name = "About"/></NavLink>
                <NavLink className="Nav_link"   to = '/Projects'> <HeaderButton name = "Projects"/></NavLink>
            </nav>
        
        );
    }

}
